
import { DataService } from './data.service';
import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import { AngularFireDatabaseModule, AngularFireDatabase } from 'angularfire2/database';

@Injectable()
export class DynamicFormService extends DataService {

  constructor(http: Http,private db:AngularFireDatabase) {
    super('https://mmsaleh.com/api/values', http);
  }

  get1(text:string,language:number)
  {
    return this.db.object('/Organizations/PortalOrg/forms/' + text + '/' + language ).valueChanges();
  }
}



