import { ControlBase } from "./../controlBase";
import { Component, OnInit, Input, forwardRef } from "@angular/core";
import { LookupsService } from "../../../Services/lookups.service";
import {
  NG_VALUE_ACCESSOR,
  AbstractControl,
  ControlValueAccessor
} from "@angular/forms";
import { ListControlBase } from "../ListControlBase";
import { utilities } from "../../../common/utilities";

@Component({
  selector: "dynamic-dropdownlist",
  templateUrl: "./dynamic-dropdownlist.component.html",
  styleUrls: ["./dynamic-dropdownlist.component.css"],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DynamicDropdownlistComponent),
      multi: true
    }
  ]
})
export class DynamicDropdownlistComponent extends ListControlBase
  implements OnInit, ControlValueAccessor {
  // @Input("label") label:string;
  targetEntity:string="";
  constructor(private lookupService: LookupsService) {
    super(lookupService);
  }

  ngOnInit() {
    if (this.entityAttributes.formType == 2) {
      let ctrl: AbstractControl = this.parent.get(this.fieldName);
      this.targetEntity=ctrl.targetEntity;
      this.lookupService
        .get(
          "?entityName=" +
            ctrl.targetEntity +
            "&language=" +
            this.entityAttributes.language
        )
        .subscribe(response => {
          this.options = response;
          this.dataLoaded = true;
          // console.log(this.options);
        });
    }
  }

  writeValue(obj: any): void {
    if (
      obj &&
      obj.value &&
      this.options &&
      this.options.filter(option => option.key === obj.key).length > 0
    ) {
      // console.log("obj key " + obj.key);
      // console.log("obj value " + obj.value);

      this.targetEntity=obj.value;

      this._value = obj.key;
      return;
    } else if (obj && obj.key && obj.value) {
      this.targetEntity=obj.value;
      this.lookupService
        .get(
          "?entityName=" +
            obj.value +
            "&id=" +
            obj.key +
            "&language=" +
            this.entityAttributes.language
        )
        .subscribe(response => {
          this.dataLoaded = false;
          //  console.log(response);
          //  console.log(this.options[0]);
          if (this.options.filter(option => option.key === obj.key).length == 0)
            this.options.push(response);
          this._value = response.key;
          this.dataLoaded = true;
        });
    }
  }

 getIcon()
 {
    return  utilities.getIcon(this.targetEntity);
 }
  test() {
    //let ctrl: AbstractControl = this.parent.get(this.fieldName);
  }
}




