import { element } from "protractor";
import { FormTabComponent } from "./form.components/form-tab/form-tab.component";
import { DynamicFieldComponent } from "./form.components/dynamic-field/dynamic-field.component";
import {
  Component,
  OnInit,
  Input,
  ViewChild,
  AfterViewInit
} from "@angular/core";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
  FormArray,
  AbstractControl
} from "@angular/forms";
import { DynamicFormService } from "../Services/dynamic-form.service";
import { ActivatedRoute, Router } from "@angular/router";
import { OperationsService } from "../Services/operations.service";

enum FormType {
  Form = 2,
  QuickView = 1
}

@Component({
  selector: "dynamic-form",
  templateUrl: "./dynamic-form.component.html",
  styleUrls: ["./dynamic-form.component.css"]
})
export class DynamicFormComponent implements OnInit, AfterViewInit {
  @Input("formID") _formID: string = "";
  @Input("formType") formType: FormType = FormType.Form;
  @Input("recordID") recordID: string = "";
  // @Input("language") language: number = 1033;
  entityAttributes: any = { formType: 0 }; //={entityName:"",entityCode:1};
  disabled: boolean;
  _rtl: string;

  set formID(value) {
    this._formID = value.toLowerCase();
  }
  get formID() {
    return this._formID;
  }

  get rtl() {
    if (this.entityAttributes.language == 1025) {
      return "rtl";
    } else {
      return "ltr";
    }
  }

  @ViewChild("Tab") Tab;

  form = new FormGroup({
    controls: new FormArray([]),
    control: new FormControl()
  });

  CRMForm: any[];

  constructor(
    private service: DynamicFormService,
    private activeRouter: ActivatedRoute,
    private router: Router,
    private operations: OperationsService
  ) {}

  ngOnInit() {
    if (!this.entityAttributes.language) {
      this.entityAttributes.language = 1033;
    }

    if (this.formType == FormType.Form) {
      this.activeRouter.paramMap.subscribe(params => {
        this.formID = params.get("id");
        let lang = +params.get("lang");
        
        
        if (lang) this.entityAttributes.language = lang;

        this.service
          .get1(
            this.formID,this.entityAttributes.language
            // "?id=" + this.formID + "&language=" + this.entityAttributes.language
          )
          .subscribe(response => {
            
            
            this.handleResonse(response);
            // this.getGata("49A0E5B9-88DF-E311-B8E5-6C3BE5A8B200");
          });
      });
    } else {
     
      this.service
        .get1(
          this.formID.toLowerCase(),this.entityAttributes.language
        )
        .subscribe(response => {                    
          this.handleResonse(response, 1);
        });
      
    }
  }

  getGata(recordId: string) {
    let obj = {
      entityName: this.entityAttributes.entityName,
      id: recordId,
      attributes: []
    };

    Object.keys(this.form.controls).forEach(key => {
      let ctrl: AbstractControl = this.form.get(key);
      obj.attributes.push({
        key: key,
        type: ctrl.controlType
      });
    });

    this.operations.getRecord(obj).subscribe(response => {
      // console.log(response);
      response.forEach(kv => {
        let ctrl: AbstractControl = this.form.get(kv.key);
        if (ctrl != null) {
          if (kv.value.value != null) {
            // console.log(kv.value);
            ctrl.targetEntity = kv.value.key;
            ctrl.setValue(kv.value);
          } else {
            ctrl.setValue(kv.value);
          }
        }
      });
    });
  }

  handleResonse(response: any, formType: number = 2) {
    this.CRMForm = response.tabs;

    this.entityAttributes["entityName"] = response.entityName;
    this.entityAttributes["entityCode"] = response.entityCode;
    this.entityAttributes["formType"] = formType == 2 ? 2 : this.formType;

    this.form = this.buildForm(response.tabs);
  }

  buildForm(formTabs) {
    let group: any = {};
    if (formTabs)
      formTabs.forEach(tab => {
        if (tab.columns)
          tab.columns.forEach(column => {
            if (column.sections)
              column.sections.forEach(section => {
                if (section.rows)
                  section.rows.forEach(row => {
                    row.forEach(control => {
                      if (control.control != null) {
                        let newControl = new FormControl(null);
                        if (this.formType != FormType.Form)
                          newControl.disable();
                        newControl.controlType = control.control.classid;
                        newControl.relatedEntities =
                          control.control.relatedEntities;

                        if (
                          newControl.relatedEntities != null &&
                          newControl.relatedEntities.length > 0
                        )
                          newControl.targetEntity =
                            newControl.relatedEntities[0];

                        if (control.control != null) {
                          group[control.control.id] = newControl;
                        }
                      }
                    });
                  });
              });
          });
      });
    return new FormGroup(group);
  }

  ngAfterViewInit() {    
  }

  submit(form: FormGroup) {
    

    let obj = {
      entityName: this.entityAttributes.entityName,
      attributes: []
    };

    Object.keys(this.form.controls).forEach(key => {
      let ctrl: AbstractControl = this.form.get(key);

      if (ctrl.dirty) {
        obj.attributes.push({
          key: key,
          value: ctrl.value,
          type: ctrl.controlType,
          targetEntity: ctrl.targetEntity
        });
      }
    });

    this.service.create(obj).subscribe(response => {
      // console.log(response);
    });
  }
}



