import { Input } from "@angular/core";
import { FormGroup } from "@angular/forms";

export class ControlBase
{

  @Input("label") label: string;
  @Input("fieldName") fieldName:string;
  @Input("name") name: string;
  @Input("type") type: string;
  @Input("placeholder") placeholder: string;
  @Input("options") options:any[]=[];
  @Input("parent") parent: FormGroup;
  @Input("required") required:number;
  @Input("maxLength") maxlength:number=1000;
  @Input("minLength") minlength:number=0; 
  @Input("formID") formID:number=0; 
  @Input("disabled") disabled:boolean; 
  @Input("viewId") viewId:String; 
  @Input("entityName") entityName:String; 
  @Input("entityAttributes") entityAttributes:any; 
  @Input("value") _value:any;
  @Input("field") field:any;

  constructor()
  {}

  set value(value)
  {
      this._value=value;
      this.propagateChange(value);
  }

  get value()
  {
      return this._value;
  }

  propagateChange = (_: any) => {};
  toched = (_:any)=>{};
  
}


