import { Component, OnInit,Input } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'form-column',
  templateUrl: './form-column.component.html',
  styleUrls: ['./form-column.component.css']
})
export class FormColumnComponent implements OnInit {
  
  
  @Input("sections") sections:any[];
  @Input("parent") parent:FormGroup;
  @Input("showlabel") showlabel:string;
  @Input("visible") visible:string;
  @Input("width") width:string="100px"; 
  @Input("disabled") disabled:boolean; 
  @Input("entityAttributes") entityAttributes:any;

  set customWidth(value)
  {

  }
  get customWidth()
  {
    return "500px" ;
    //  return (+this.width.replace("%",""));
  }

  constructor() { }

  ngOnInit() {
  }

}



