import { AfterViewInit, ViewChild } from '@angular/core';
import { FormGroup, FormArray, FormControl } from '@angular/forms';
import { Component, OnInit,Input } from '@angular/core';

@Component({
  selector: 'form-tab',
  templateUrl: './form-tab.component.html',
  styleUrls: ['./form-tab.component.css'] 
})
export class FormTabComponent implements OnInit {
  
    @Input("labels") labels:string;
    @Input("id") id:string;
    @Input("name") name:string;
    @Input("showlabel") showlabel:boolean;
    @Input("visible") visible:boolean;
    @Input("columns") columns:any[];
    @Input("parent") parent:FormGroup;
    @Input("disabled") disabled:boolean; 
    @Input("entityAttributes") entityAttributes:any; 
    
    get customWidth()
    {
      return ((100/this.columns.length)).toString() + '%';
      //  return (+this.width.replace("%",""));
    }
  constructor() {
  

  }

  ngOnInit() {
    
    
  }

}



