import { DyanmicGridService } from "./Services/dyanmic-grid.service";
import { TabViewModule } from "primeng/tabview";
import { DynamicDropdownlistComponent } from "./dynamic-form/form.components/dynamic-dropdownlist/dynamic-dropdownlist.component";
import { LookupsService } from "./Services/lookups.service";
import { ProgressBarModule } from "primeng/progressbar";
import { OptionsetsService } from "./Services/optionsets.service";
import { DynamicOptionsetComponent } from "./dynamic-form/form.components/dynamic-optionset/dynamic-optionset.component";
