import { ProfileComponent } from './profile/profile/profile.component';
import { PageHeaderSimpleComponent } from './page/page-header-simple/page-header-simple.component';
import { AddFormComponent } from './admin/add-form/add-form.component';




// import {FormsModule} from '@angular/forms/forms'

@NgModule({
  declarations: [
    AppComponent,
    DynamicFormComponent,
    DynamicFieldComponent,
    FormTabComponent,
    FormSectionComponent,
    FormColumnComponent,
    SubGridComponent,
    DynamicDropdownlistComponent,
    DynamicOptionsetComponent,
    DynamicMenuComponent,
    NotFoundComponent,
    HomeComponent,
    TestComponent,
    Test2Component,
    ListviewComponent,
    LeftSideComponent,
    NotificationsComponent,
    PageFooterComponent,
    PageHeaderComponent,
    ProfileTimelineComponent,
    PhotoHeaderComponent,
    ProfessionalCareerComponent,
    SkillsComponent,
    ProfileComponent,
    PageHeaderSimpleComponent,
    AddFormComponent,
  ],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    MatInputModule,
    MatCheckboxModule,
    MatSelectModule,
    HttpModule,
    FormsModule,
    InputTextModule,
    ButtonModule,
    AccordionModule,
    GMapModule,
    DataTableModule,
    TabViewModule,
    ProgressBarModule,
    MenubarModule,
    MatTabsModule,
    MatProgressBarModule,
    MatDatepickerModule,
    MatButtonModule,
    MatIconModule,    
    MatTableModule,
    MatPaginatorModule,
    MatExpansionModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    
    RouterModule.forRoot([      
      {
        path:'',
        component:ProfileComponent
      },
      {
        path:'home',
        component:HomeComponent
      },
      {
        path: 'form',
        component: DynamicFormComponent
      },
      {
        path: 'aboutme',
        component: ProfileComponent
      },      
      {
        path:'form/:id/:lang',
        component:DynamicFormComponent      
      },
      {
        path:'form/:id',
        component:DynamicFormComponent      
      },
      {
        path:"test2",
        component:Test2Component
      },
      {
        path:'**',
        component:NotFoundComponent
      }
    ])
  ],
  providers: [
    DynamicFormService,
    DyanmicGridService,
    LookupsService,
    OptionsetsService,    
    OperationsService    
  ],
  bootstrap: [AppComponent],
  
})
export class AppModule {}



