import { ControlBase } from "./../controlBase";
import { Component, OnInit, Input } from "@angular/core";
import { DyanmicGridService } from "../../../Services/dyanmic-grid.service";

@Component({
  selector: "sub-grid",
  templateUrl: "./sub-grid.component.html",
  styleUrls: ["./sub-grid.component.css"]
})
export class SubGridComponent extends ControlBase implements OnInit {
  @Input("gridHeader") gridHeader: String = "";
  @Input("viewId") viewId: string;
  @Input("isSimple") isSimple: boolean=false;

  fields: any[];
  data: any[];
  constructor(private service: DyanmicGridService) {
    super();
    // console.log(this.gridHeader);
    // console.log(this.label);
  }

  customSort(eventArgs) {}

  ngOnInit() {
    if (this.field) {
      this.fields = this.field.control.SubGrid.row.cell;
      this.service.get("?id=" + this.viewId).subscribe(data => {
        if(data)
          this.data = JSON.parse(data);
      });
    } else {
      this.isSimple=true;
      this.service      
        .get("?id=" + this.viewId + "&language=1033")
        .subscribe(headers => {
          // this.fields = response;

          if (headers) {
            var obj = JSON.parse(headers);
            if (this.gridHeader == "") {
              this.gridHeader = obj.name;
            }
            this.fields = obj.row.cell;

            this.service.get("?id=" + this.viewId).subscribe(data => {
              this.data = JSON.parse(data);
            });
          }
        });
    }
  }

  getRecordsCountPerPage()
  {
    return this.isSimple?10:5;
  }
}



