import { BadInputException } from './../common/bad-input-exception';
import { NotFoundException } from './../common/not-found-exception';
import { GenericEcxeption } from './../common/generic-exception';
import { Http, RequestOptions, RequestMethod, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/observable/throw';

@Injectable()
export class DataService {
  constructor(private url: string, private http: Http) { }

  getAll() {
    return this.http.get(this.url)
      .map(response => response.json())
      .catch(this.handleError);
  }

  get(id:string) {
    let urlPrefix:string="";
    if(id)
    {
       if(id.startsWith("?")==false)
        {urlPrefix="\\"}

      return this.http.get(this.url + urlPrefix + id)
      .map(response => response.json())
      .catch(this.handleError);
    }
  
    
  }

  getRecord(resource:any) {
           
    let head = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions();
    options.headers=head;
   

   return this.http.post(this.url, JSON.stringify(resource),options)
     .map(response => response.json())
     .catch(this.handleError);
  
    
  }

  create(resource) {
     let head = new Headers({ 'Content-Type': 'application/json' });
     let options = new RequestOptions();
     options.headers=head;
    

    return this.http.post(this.url, JSON.stringify(resource),options)
      .map(response => response.json())
      .catch(this.handleError);
  }

  update(resource) {
    return this.http.patch(this.url + '/' + resource.id, JSON.stringify({ isRead: true }))
      .map(response => response.json())      
      .catch(this.handleError);
  }

  delete(id) {
    return this.http.delete(this.url + '/' + id)
      .map(response => response.json())
      .toPromise()
      .catch(this.handleError);
  }

  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInputException(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundException());
    
    return Observable.throw(new GenericEcxeption(error));
  }
}



