


export class utilities
{

    static getIcon(text:string) {
    // return "";
        if(text==undefined)
            return "view_compact";

        
        let icon:string=""
        switch (text) {
          case "account":
            icon= "work";
            break;
          case "contact":
            icon= "person";
            break;
          case "contractdetail":
            icon= "person";
            break;
          case "incident":
            icon= "build";
            break;
          case "kbarticle":
            icon= "book";
            break;
          case "subject":
            icon= "subject";
            break;
          case "product":
            icon= "shopping_cart";
            break;
          case "entitlement":
            icon= "import_contacts";
            break;
          default:
            icon= "";
            break;
        }

        if(icon)
            return icon;
        
        if(icon=="")
        {
          let fieldName=text.toLowerCase();
            // alert(fieldName);
          if(fieldName.indexOf("mobile")>0)
          {
            icon="smartphone";
          }else if(fieldName.indexOf("phone")>0)
          {
            icon="phone";
          }else if(fieldName.indexOf("email")>0)
          {
            icon="email";
          }else if(fieldName.indexOf("address")>0)
          {
            icon="location_on";
          }else if(fieldName.indexOf("note")>0)
          {
            icon="note";
          }else if(fieldName.indexOf("desciption")>0)
          {
            icon="description";
          }else if(fieldName.indexOf("date")>0)
          {
            icon="date_range";
          }else if(fieldName.indexOf("title")>0)
          {
            icon="subtitles";
          }else if(fieldName.indexOf("subject")>0)
          {
            icon="subject";
          }else if(fieldName.indexOf("number")>0)
          {
            icon="format_list_numbered";
          }else if(fieldName.indexOf("amount")>0)
          {
            icon="attach_money";
          }else if(fieldName.indexOf("currency")>0)
          {
            icon="attach_money";
          }else if(fieldName.indexOf("duration")>0)
          {
            icon="access_time";
          }
        } 
        if(icon=="")
            return "view_compact";
        else
        {               
             return icon;
        }
      }
    
}


